
$FORM_EL_SIZE: 50px;

header {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  background-color: #333;

  .collection_select {
    height: $FORM_EL_SIZE;
    flex-grow: 1;
    padding: 0 10px;
    border: 1px solid #ddd;
  }
}

.webtoon_list {
  > li {
    border: 1px solid #dddddd;
    display: flex;
    align-items: center;
    flex-wrap: nowrap;
    background-color: white;
    height: $FORM_EL_SIZE;

    + li {
      margin-top: 10px;
    }

    > * {
      height: 100%;
    }

    > .삭제 {
      width: $FORM_EL_SIZE;
      min-width: $FORM_EL_SIZE;
      background-color: darkred;
      color: white;
    }

    > .제목 {
      padding: 0 5px;
      width: calc(100% - 50px * 3);
      display: flex;
      align-items: flex-start;
      flex-direction: column;
      justify-content: center;

      > .text_0 {
        color: #999999;
        font-size: 12px;
        width: 100%;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
      }

      > .text_1 {
        margin-top: 3px;
        color: #333333;
        font-size: 14px;
        width: 100%;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
      }
    }

    > .회차 {
      width: $FORM_EL_SIZE;
      min-width: $FORM_EL_SIZE;
      text-align: center;
      border: none;
      border-left: 1px solid #dddddd;
      -webkit-appearance: none;
      -moz-appearance: textfield;
    }

    > .저장 {
      width: $FORM_EL_SIZE;
      min-width: $FORM_EL_SIZE;
      background-color: dodgerblue;
      color: white;

      &.disabled {
        pointer-events: none;
        background-color: black;
        opacity: 0.2;
      }
    }
  }
}

footer {
  height: 100px;
  .create_webtoon_item_box {
    padding: 10px;
    background-color: #333;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;

    display: flex;
    flex-wrap: nowrap;
    align-items: center;

    > input {
      display: block;
      font-size: 16px;
      width: 100%;
      height: $FORM_EL_SIZE;
      padding: 0 10px;
    }

    > .create_btn {
      min-width: $FORM_EL_SIZE;
      width: $FORM_EL_SIZE;
      height: $FORM_EL_SIZE;
      background-color: green;
      color: white;
    }
  }
}
